<h1 id="python-abstraction" tabindex="-1"><a class="header-anchor" href="#python-abstraction" aria-hidden="true">#</a> Python Abstraction</h1>
<p>Why is programming so hard to learn? One of the reasons is probably because you haven't learned how to make use of abstraction yet. In the programs we create, there are many details we don't need to understand to understand how our program works, but new programmers usually get stuck at these details they don't need to worry about. Abstraction is about not caring about these unnecessary implementation details, and experienced programmers use it all the time. Let's take a look at some examples.</p>
